<template>
  <div>
    <v-layout row>
      <v-flex md6
              offset-md3>
        <v-card class="mt-2 pb-3">
          <v-card-title primary-title
                        class="pl-5 mb-3">
            <div>
              <h3 class="headline mb-0 font-weight-medium font-italic"
                  color="primary">New Restore Request</h3>
            </div>
          </v-card-title>
          <form class="px-5">
            <v-text-field v-model="changeNumber"
                          :error-messages="changeNumberErrors"
                          label="Change Number"
                          hint="Example 123456 not C123456"
                          type="number"
                          required
                          clearable
                          autofocus
                          @input="$v.changeNumber.$touch()"
                          @blur="$v.changeNumber.$touch()"></v-text-field>

            <v-autocomplete v-model="sourceServer"
                            :items="sourceSrvList"
                            :error-messages="sourceSrvErrors"
                            label="Source Server"
                            required
                            dense
                            clearable
                            @change="$v.sourceServer.$touch()"
                            @blur="$v.sourceServer.$touch()"></v-autocomplete>

            <v-autocomplete v-model="sourceDatabase"
                            :items="sourceDBList"
                            :error-messages="sourceDBErrors"
                            label="Source Database"
                            required
                            dense
                            clearable
                            @change="$v.sourceDatabase.$touch()"
                            @blur="$v.sourceDatabase.$touch()"
                            @click="GetSourceDBs"></v-autocomplete>

            <v-autocomplete v-model="targetSrv"
                            :items="targetSrvList"
                            :error-messages="targetSrvErrors"
                            label="Target Server"
                            required
                            dense
                            clearable
                            @change="$v.targetSrv.$touch()"
                            @blur="$v.targetSrv.$touch()"></v-autocomplete>

            <v-layout row>
              <v-switch label="Original DB Name"
                        color="secondary"
                        v-model="originalDB"></v-switch>

              <v-switch label="Daily Restore"
                        color="secondary"
                        v-model="daily"></v-switch>

              <v-switch label="Edit File Paths"
                        color="secondary"
                        v-model="path"></v-switch>

            </v-layout>

            <v-layout row justify-space-between v-if="path">
                <v-text-field v-model="DBPath"
                          label="DB Path"
                          class="mr-5"
                          >
                </v-text-field>

                <v-text-field v-model="LogPath"
                          label="Log Path">
                </v-text-field>
            </v-layout>

            <v-text-field v-model="targetDB"
                          :error-messages="targetDBErrors"
                          label="Target DB"
                          hint="C123456"
                          required
                          @input="$v.targetDB.$touch()"
                          @blur="$v.targetDB.$touch()"></v-text-field>
            <v-layout row
                      justify-space-between>
              <v-menu ref="DateMenu"
                      :close-on-content-click="false"
                      v-model="DateMenu"
                      :nudge-right="40"
                      :return-value.sync="date"
                      lazy
                      transition="scale-transition"
                      offset-y
                      min-width="290px">
                <v-text-field slot="activator"
                              v-model="date"
                              :error-messages="dateErrors"
                              label="Select Date"
                              prepend-icon="event"
                              readonly
                              clearable
                              @input="$v.date.$touch()"
                              @blur="$v.date.$touch()"></v-text-field>
                <v-date-picker v-model="date"
                               header-color="secondary"
                               @input="$refs.DateMenu.save(date)"></v-date-picker>

              </v-menu>
              <v-menu ref="TimeMenu"
                      :close-on-content-click="false"
                      v-model="TimeMenu"
                      :nudge-right="40"
                      :return-value.sync="time"
                      lazy
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="290px">
                <v-text-field slot="activator"
                              v-model="time"
                              :error-messages="timeErrors"
                              label="Select Time"
                              prepend-icon="access_time"
                              readonly
                              clearable
                              @input="$v.time.$touch()"
                              @blur="$v.time.$touch()"></v-text-field>
                <v-time-picker v-if="TimeMenu"
                               v-model="time"
                               header-color="secondary"
                               @change="$refs.TimeMenu.save(time)"></v-time-picker>
              </v-menu>
            </v-layout>
            <v-btn @click="submit"
                   :disabled="this.$v.$invalid"
                   color="secondary"
                   class="ml-3"
                   fab>
              Go!
            </v-btn>
          </form>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import {
  GetSourceDatabases,
  GetSourceServers,
  GetDestinationServers,
  CreateRestore
} from "@/services/api/SQLRestores";

import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";
import { Notify } from "@/services/HelperService";

export default {
  mixins: [validationMixin],

  validations: {
    changeNumber: { required },
    sourceServer: { required },
    sourceDatabase: { required },
    targetSrv: { required },
    targetDB: { required },
    date: { required },
    time: { required }
  },

  data() {
    return {
      date: null,
      time: null,
      TimeMenu: false,
      DateMenu: false,
      originalDB: false,
      daily: false,
      path: false,
      DBPath: null,
      LogPath: null,
      changeNumber: null,
      sourceServer: null,
      sourceDatabase: null,
      targetSrv: null,
      targetDB: "",
      sourceSrvList: [],
      sourceDBList: [],
      targetSrvList: []
    };
  },

  watch: {
    // whenever originalDB changes, this function will run
    originalDB(newValue) {
      newValue == true
        ? (this.targetDB = this.sourceDatabase)
        : (this.targetDB = "");
    }
  },

  computed: {
    sourceSrvErrors() {
      const errors = [];
      if (!this.$v.sourceServer.$dirty) return errors;
      !this.$v.sourceServer.required && errors.push("Server is required");
      return errors;
    },
    sourceDBErrors() {
      const errors = [];
      if (!this.$v.sourceDatabase.$dirty) return errors;
      !this.$v.sourceDatabase.required && errors.push("Database is required");
      return errors;
    },
    targetSrvErrors() {
      const errors = [];
      if (!this.$v.targetSrv.$dirty) return errors;
      !this.$v.targetSrv.required && errors.push("Target Server is required");
      return errors;
    },
    targetDBErrors() {
      const errors = [];
      if (!this.$v.targetDB.$dirty) return errors;
      /*       !this.$v.targetDB.minLength &&
        errors.push("Target DB must be at least 2 characters long"); */
      !this.$v.targetDB.required && errors.push("Target DB is required.");
      return errors;
    },
    changeNumberErrors() {
      const errors = [];
      if (!this.$v.changeNumber.$dirty) return errors;
      !this.$v.changeNumber.required &&
        errors.push("Change Number is required.");
      return errors;
    },
    dateErrors() {
      const errors = [];
      if (!this.$v.date.$dirty) return errors;
      !this.$v.date.required && errors.push("Date is required.");
      return errors;
    },
    timeErrors() {
      const errors = [];
      if (!this.$v.time.$dirty) return errors;
      !this.$v.time.required && errors.push("Time is required.");
      return errors;
    }
  },

  methods: {
    submit() {
      CreateRestore({
        SourceServerName: this.sourceServer,
        SourceDatabaseName: this.sourceDatabase,
        DestinationServerName: this.targetSrv,
        DestinationDatabaseName: this.targetDB,
        ChangeNumber: this.changeNumber,
        PlannedRestoreDate: new Date(`${this.date} ${this.time}`),
        DailyRestore: this.daily,
        DBPath: this.DBPath,
        LogPath: this.LogPath
      })
        .then(() => {
          this.$notify({
            group: "notify",
            type: "success",
            title: "Restore created successfully!"
          });
          this.$router.push("/SQLServer/RestoresPending");
        })
        .catch(error => {
          this.$notify(Notify(error));
        });
    },
    clear() {
      this.$v.$reset();
      this.changeNumber = null;
      this.sourceServer = null;
      this.sourceDatabase = null;
      this.targetSrv = null;
      this.targetDB = null;
      this.date = null;
    },
    GetSourceDBs() {
      GetSourceDatabases(this.sourceServer).then(data => {
        this.sourceDBList = Object.keys(data).map(
          i => data[i].sourceDatabaseName
        );
      });
    }
  },

  mounted() {
    GetSourceServers().then(data => {
      this.sourceSrvList = Object.keys(data).map(i => data[i].sourceServerName);
    });

    GetDestinationServers().then(data => {
      this.targetSrvList = Object.keys(data).map(
        i => data[i].destinationServerName
      );
    });
  }
};
</script>

<style lang="scss" scoped>
.v-card {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.v-card__title {
  background-color: #7b507b;
  color: white;
  background: #7b507b; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #7b507b,
    #7b507bd3
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom right, #7b507b, #7b507bd3);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
</style>
