import { HTTP } from "@/main";

export const GetSourceDatabases = servername => {
  return HTTP.get("/sql/sourcedatabases", {
    params: {
      srv: servername
    }
  }).then(response => {
    return response.data;
  });
};

export const GetSourceServers = () => {
  return HTTP.get("/sql/SourceServers").then(response => {
    return response.data;
  });
};

export const GetDestinationServers = () => {
  return HTTP.get("/sql/DestinationServers").then(response => {
    return response.data;
  });
};

export const GetAllRestores = () => {
  return HTTP.get("/sql/AllRestores").then(response => {
    return response.data;
  });
};

export const CreateRestore = payload => {
  return HTTP.post("/sql/CreateRestore", payload);
};

export const CancelRestore = payload => {
  return HTTP.post("/sql/CancelRestore", payload).then(response => {
    return response.data;
  });
};
